*[class*=-label-] {
  cursor: default;
}
*[class*=-label-] * {
  cursor: default;
}

.a-label-question {
  color: #455B97;
  padding: 0;
  margin: 0;
}

.focused * {
  outline: none;
}
.focused .a-label-postlabel {
  border: 1px solid #455B97;
  border-left-color: #8797C8;
}
.focused .a-label-prelabel {
  border: 1px solid #455B97;
  border-right-color: #8797C8;
}

.m-input-singlelineedit .a-label-postlabel {
  display: inline-block;
  border: 1px solid #8797C8;
  border-radius: 0 10px 10px 0;
  position: relative;
  left: -0.5em;
  background-color: #FAFAFA;
  padding: 10px 16px;
  color: #A0ABCC;
  white-space: nowrap;
}
.m-input-singlelineedit .a-label-prelabel {
  display: inline-block;
  border: 1px solid #8797C8;
  border-radius: 10px 0 0 10px;
  background-color: #FAFAFA;
  padding: 10px 16px;
  color: #A0ABCC;
  white-space: nowrap;
}

.o-question-hnumberslider .a-label-prelabel {
  position: absolute;
  left: 0;
  bottom: -0.8em;
  color: #455B97;
}
.o-question-hnumberslider .a-label-postlabel {
  position: absolute;
  right: 0;
  bottom: -0.8em;
  color: #455B97;
}
.o-question-hnumberslider .active .a-label-thumbvalue {
  visibility: visible;
}
.o-question-hnumberslider .a-label-thumbvalue {
  box-sizing: border-box;
  color: #212C4C;
  position: absolute;
  top: -1.1em;
  visibility: hidden;
  transform: translateX(-50%);
}
.o-question-hnumberslider.has-value .a-label-thumbvalue {
  visibility: visible;
}
.o-question-hnumberslider .m-style-slidermarks {
  box-sizing: border-box;
  position: absolute;
  z-index: 2;
  width: calc(100% - 12px);
  left: 6px;
  height: 16px;
  top: 16px;
  border-radius: 16px;
}
.o-question-hnumberslider .m-label-ticklabels {
  position: relative;
  top: -0.5em;
  box-sizing: border-box;
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: #8797C8;
}
.o-question-hnumberslider .m-label-ticklabels > span {
  text-align: center;
  flex: 1 1 0;
}

.m-option-base input:checked + label > .a-label-question {
  color: #449B9B;
}
.m-option-base .a-label-question {
  display: inline-block;
  padding-left: 1.5em;
}

